import React, {useRef} from "react";
import "./contact.css";
import emailjs from "@emailjs/browser";
import svg1 from "../../images/Sent.svg";
const Contact = () => {
  const form = useRef();
  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_7ec04bh",
        "template_b14hwd1",
        form.current,
        "7zkJDXL_eofKXMay9"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
      e.target.reset()
  };
  return (
    <div id="contact">
      <h1 className="c-title">Contact Me</h1>

      <div className="C-div">
        <div className="c-left-div">
          <img src={svg1} alt="" />
        </div>
<div>
  
</div>
        <div className="c-right-div">
          <h2>Have an idea?
Let's make something great</h2>

          <form  ref={form} onSubmit={sendEmail}>
            {/* <label>Name</label> */}
            <input type="text" name="user_name"  placeholder="Name"/>
            {/* <label>Email</label> */}
            <input type="email" name="user_email"  placeholder="Email"/>
            {/* <label>Message</label> */}
            <textarea name="message"  placeholder="Massage"/>
            <input className="s-btn" type="submit" value="Send" />
          </form>
        </div>
      </div>
    </div>
  );
};

export default Contact;
