import React from "react";
// import logo from "../../images/logo.png"
import "./navbar.css";
import {Link} from "react-scroll";
// import  NavLink  from 'react-router-dom'
const Navbar = () => {
  return (
    <>
      <div className="n-bar">
        <div className="n-left">
          <h1>Toogle</h1>

          <h1>Dipak</h1>
        </div>

        <div className="n-right">
          <ul className="n-ul-list">
            <li>
              <Link activeClass="active" to="home"  offset={-100} spy={true} smooth={true}>
                Home
              </Link>
            </li>
            <li>
              <Link activeClass="active" to="About" offset={-100} spy={true} smooth={true}>
                About
              </Link>
            </li>
            <li>
              <Link activeClass="active" to="education"  offset={-100} spy={true} smooth={true}>
                Education
              </Link>
            </li>

            <li>
              <Link activeClass="active" to="projects"  offset={-60} spy={true} smooth={true}>
                Projects
              </Link>
            </li>
            <li>
              <Link activeClass="active" to="skills"  offset={-100} spy={true} smooth={true}>
                Teck stack
              </Link>
            </li>
            <li>
              <Link activeClass="active" to="contact"  offset={-100} spy={true} smooth={true}>
                {" "}
                <button className="contact-button">Contect Me</button>
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </>
  );
};

export default Navbar;
